.mainScreen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


h1 {
    margin: 0;
    padding: 0;
    padding: 2%;
    font-size: 1.6rem;
}

h2 {
    margin: 0;
    padding: 0;
    font-size: 1.1rem;
}

.title {
    font-family: 'CasinoTitle';
    font-size: 3rem;
    color: rgb(109, 187, 95);

}


.header {
    display: flex;
    background-color: rgb(109, 187, 95);
    width: 100%;
    justify-content: center;
}

.headerLogo {

    width: 8%;
    animation: spin 25s linear infinite;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.header div {
    display: inline;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin-inline: 1%;

}

.walletStats {
    color: white;
    font-size: 1.2rem;
    margin: auto;
}

.walletStats p {
    margin: auto;
    padding: auto;
}



.createGame {
    background-color: rgb(255, 255, 255);
    width: 8rem;
    border: 3px solid black;
    border-style: dotted;

}

.createGame input {
    pointer-events: visible;
}

.createGame:hover {
    background-color: rgb(255, 255, 144);
    cursor: pointer;
    scale: 1.2;
}

.betAmountInputBox {
    width: 80%;

    margin: 8px;
    font-size: large;
    text-align: center;
    box-sizing: border-box;
 

}

.gameGrid {
    display: grid;
    list-style: none;
    padding: 0;
    margin: 0px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(auto-fill, minmax(0px, auto));
    grid-gap: 20px;
    justify-content: center;

}

.gameGrid .game {
    margin: 0;
    padding: 0;
}

.myGames {
    margin-top: 2%;
}

.openGames {
    margin-top: 2%;
}


.openGames .gameGrid li:hover {
    cursor: pointer;
    scale: 1.2;
    background-color: rgb(109, 187, 95);

}

.myGames .gameGrid li:hover {
    cursor: pointer;
    scale: 1.2;
    background-color: rgb(187, 95, 95);

}


.gameGrid .game {

    border: 3px solid black;
    border-style: dotted;
    margin-block: 15%;

    width: 6rem;




}

.gameGrid .game p {

    font-size: 1.1rem;
    background-color: white;

}

.spanType1 {

    color: black;

}

.spanType1 {

    color: black;

}

.lastGame {
    padding-top: 3%;
}

.lastGame ul {
    list-style: none;
    padding: 0;
    margin: 0;
}




.rolls {
    display: flex;
}

p {
    margin: 0;
    padding: 0;
}

input {
    margin-bottom: 1rem;
}